.product {
    display: flex;
    flex-direction: column;
    border: 1px solid var(--tg-theme-button-color);
    padding: 15px;
    width: 170px;
}


.img {
    width: 100%;
    background: lightgray;
    height: 100px;
    margin: 0 auto;
    background: var(--tg-theme-button-color);
}

.description {
    font-size: 0.8em;
    color: var(--tg-theme-hint-color);
    height: 50px;
    overflow-y: scroll;
}

.add-btn {
    border-radius: 10px;
    width: 40px;
    height: 40px;
}

.subtract-btn {
    border-radius: 10px;
    width: 40px;
    height: 40px;
}

.button-section {
    flex-direction: row;
    bottom: 10px;
}

.quantity {
    font-weight: bold;
}

counter {
    flex-direction: row;
    margin: 20px;
}
